import {FunctionComponent} from 'react'
import {graphql} from 'gatsby'
import {Paper} from '@material-ui/core'
import {
  FeaturedTopicsSection,
  FeaturesSection,
  FindOutSection,
  InfoSection,
  LogosSection,
  MainSection,
  MarketingSection,
  ReviewSection,
} from '../home'

const IndexPage: FunctionComponent = () => {
  return (
    <Paper>
      <MainSection />
      <LogosSection />
      <InfoSection />
      <FindOutSection />
      <ReviewSection />
      <FeaturesSection />
      <MarketingSection />
      <FeaturedTopicsSection />
    </Paper>
  )
}

export default IndexPage

export const query = graphql`
  query HomePage {
    header: markdownRemark(frontmatter: {title: {eq: "header"}}) {
      frontmatter {
        logo {
          altText
        }
        nav {
          links {
            label
            url
          }
          buttons {
            diners
            signUp
          }
        }
      }
    }
    footer: markdownRemark(frontmatter: {title: {eq: "footer"}}) {
      frontmatter {
        copyRight
        main
        socialMedia {
          type
        }
        buttons {
          signUp {
            label
            href
          }
        }
      }
    }
    countries: markdownRemark(frontmatter: {title: {eq: "countries"}}) {
      frontmatter {
        selected
        list {
          code
          name
          shortName
          cities
        }
      }
    }
    home: markdownRemark(frontmatter: {title: {eq: "home"}}) {
      frontmatter {
        mainSection {
          text
          button
          backgroundImage {
            alt
            src {
              childImageSharp {
                gatsbyImageData(layout: FULL_WIDTH)
              }
            }
          }
        }
        logosSection {
          text
          logos {
            alt
            src {
              childImageSharp {
                gatsbyImageData(layout: CONSTRAINED)
              }
            }
          }
        }
        infoSection {
          text
          infos {
            text
            icon
            link {
              url
              label
            }
          }
        }
        findOutSection {
          square1 {
            text
            button
            image {
              src {
                childImageSharp {
                  gatsbyImageData(layout: FULL_WIDTH)
                }
              }
              alt
            }
          }
          square2 {
            text
            button
            image {
              src {
                childImageSharp {
                  gatsbyImageData(layout: FULL_WIDTH)
                }
              }
              alt
            }
          }
        }
        reviewSection {
          reviews {
            text
            image {
              src {
                childImageSharp {
                  gatsbyImageData(layout: FULL_WIDTH)
                }
              }
              alt
            }
          }
        }
        featuresSection {
          features {
            description
            title
            type
            image {
              src {
                childImageSharp {
                  gatsbyImageData(layout: FULL_WIDTH)
                }
              }
              alt
            }
          }
        }
        marketingSection {
          title
          description
          highlightText
          sub1
          sub2
          button {
            label
            href
          }
        }
        featuredTopicsSection {
          title
          topics {
            text
            image {
              src {
                childImageSharp {
                  gatsbyImageData(layout: FULL_WIDTH)
                }
              }
              alt
            }
          }
        }
      }
    }
  }
`
